"use client";

import { useEffect } from "react";
import { useConvex } from "convex/react";
import { api } from "@/convex/_generated/api";
import useAuthStore from "@/hooks/use-auth-store";

export const AuthProvider = ({ children }) => {
    const convex = useConvex();
    const { token, login, logout, isAuthenticated } = useAuthStore();

    useEffect(() => {
        const checkSession = async () => {
            if (token) {
                try {
                    const user = await convex.query(api.auth.getSessionUser, { token });
                    if (user) {
                        // Re-hydrate store with fresh user data
                        useAuthStore.setState({ user, isAuthenticated: true, role: user.role });
                    } else {
                        // Session invalid or expired
                        logout();
                    }
                } catch (error) {
                    console.error("Session re-hydration failed:", error);
                    logout();
                }
            }
        };

        checkSession();
    }, [token, convex, logout]);

    return <>{children}</>;
};
